import Image from "next/image";
import { projects } from "@/app/data/projects";
import type { Project } from "@/app/types/project";
import { FiExternalLink, FiGithub } from "react-icons/fi";

const PRIMARY_GRADIENT =
  "bg-gradient-to-br from-slate-950/95 via-slate-900/80 to-slate-950/90";

export default function WorkSection() {
  if (projects.length === 0) {
    return null;
  }

  const [featured, ...rest] = projects;

  return (
    <section id="work" className="relative isolate py-24 sm:py-28">
      <div
        className="pointer-events-none absolute inset-0 opacity-80"
        aria-hidden="true"
      >
        <div className="absolute inset-x-0 top-0 h-72 bg-[radial-gradient(circle_at_top,_rgba(16,185,129,0.35),transparent_65%)]" />
      </div>

      <div className="relative mx-auto max-w-6xl px-6 sm:px-10 lg:px-12">
        <div className="flex flex-col gap-4 sm:flex-row sm:items-end sm:justify-between">
          <div>
            <p className="font-mono text-xs sm:text-sm uppercase tracking-[0.4em] text-teal-200/70">
              03 • Work
            </p>
            <h2 className="mt-3 text-3xl sm:text-4xl md:text-5xl font-semibold tracking-tight text-white">
              Some Things I&apos;ve Built
            </h2>
            <p className="mt-3 max-w-2xl text-sm sm:text-base text-slate-400">
              A gallery of products, experiments, and systems that highlight my approach to
              resilient, experience-driven engineering.
            </p>
          </div>
          <div className="h-px w-full bg-gradient-to-r from-teal-300/40 via-white/20 to-transparent sm:h-16 sm:w-auto sm:bg-none" />
        </div>

        <div className="mt-12 grid grid-cols-1 gap-8 lg:grid-cols-5">
          <div className="lg:col-span-3">
            <ProjectCard project={featured} variant="featured" />
          </div>
          <div className="lg:col-span-2 flex flex-col gap-6">
            {rest.map((project) => (
              <ProjectCard key={project.id} project={project} variant="standard" />
            ))}
          </div>
        </div>
      </div>
    </section>
  );
}

type CardProps = {
  project: Project;
  variant: "featured" | "standard";
};

function ProjectCard({ project, variant }: CardProps) {
  const isFeatured = variant === "featured";
  const minHeight = isFeatured ? "min-h-[420px]" : "min-h-[240px]";
  const padding = isFeatured ? "p-8 sm:p-10" : "p-6 sm:p-8";

  return (
    <article
      className={`group relative overflow-hidden rounded-3xl border border-white/10 ${PRIMARY_GRADIENT} shadow-[0_35px_120px_rgba(2,6,23,0.8)] ${minHeight}`}
    >
      <BackgroundImage project={project} />

      <div className={`relative flex h-full flex-col justify-between gap-6 ${padding}`}>
        <div className="space-y-3">
          {project.subtitle && (
            <p className="font-mono text-xs uppercase tracking-[0.35em] text-teal-200/70">
              {project.subtitle}
            </p>
          )}
          <h3 className={`text-white ${isFeatured ? "text-3xl" : "text-2xl"} font-semibold transition-colors duration-200 group-hover:text-teal-100`}>
            {project.title}
          </h3>
          {project.highlight && (
            <div className="max-w-xl text-sm text-teal-200/80">
              {project.highlight}
            </div>
          )}
          <p className="max-w-2xl text-sm sm:text-base text-slate-300">
            {project.description}
          </p>
        </div>

        <div className="space-y-4">
          <div className="flex flex-wrap gap-2">
            {project.tech.map((tech) => (
              <span
                key={tech}
                className="rounded-full border border-white/10 bg-white/5 px-3 py-1 text-xs font-medium text-slate-100"
              >
                {tech}
              </span>
            ))}
          </div>

          {(project.links?.github || project.links?.external) && (
            <div className="flex items-center gap-4 text-slate-200">
              {project.links?.github && (
                <a
                  href={project.links.github}
                  target="_blank"
                  rel="noreferrer"
                  aria-label={`View ${project.title} source`}
                  className="rounded-full border border-white/10 p-3 transition hover:border-teal-300/60 hover:text-teal-200"
                >
                  <FiGithub className="h-5 w-5" />
                </a>
              )}
              {project.links?.external && (
                <a
                  href={project.links.external}
                  target="_blank"
                  rel="noreferrer"
                  aria-label={`Open ${project.title}`}
                  className="rounded-full border border-white/10 p-3 transition hover:border-teal-300/60 hover:text-teal-200"
                >
                  <FiExternalLink className="h-5 w-5" />
                </a>
              )}
            </div>
          )}
        </div>
      </div>
    </article>
  );
}

type BackgroundProps = {
  project: Project;
};

function BackgroundImage({ project }: BackgroundProps) {
  return (
    <div className="absolute inset-0">
      <div className="absolute inset-0">
        {project.image ? (
          <Image
            src={project.image}
            alt={project.title}
            fill
            className="object-cover object-center opacity-70 transition duration-500 group-hover:scale-105"
            sizes="(max-width: 768px) 100vw, (max-width: 1280px) 60vw, 700px"
            priority={project.id === projects[0]?.id}
          />
        ) : (
          <div className="h-full w-full bg-gradient-to-br from-slate-900 to-slate-950" />
        )}
      </div>
      <div className="absolute inset-0 bg-gradient-to-br from-slate-950/85 via-slate-950/60 to-slate-950/90" />
      <div className="absolute inset-0 opacity-0 transition-opacity duration-500 group-hover:opacity-20" />
    </div>
  );
}
